@import url('https://fonts.googleapis.com/css2?family=Skranji:wght@700&display=swap');
.contact-container-section{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 30px 30px;
    position: relative;
    background: #fcfcff;
}
.middle-contact-message-seccussful{
    position: absolute;
    left: 27%;
    top: 20%;
}
#myAnimation{
    width: 400px;
    height: 400px;

}
.left-description-contact-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 30px;
    padding-right: 5px;
    align-self: self-start;
}
.heading-contact{
    color: rgb(20, 20, 20);
    font-family: 'Skranji', cursive;
    font-size: 45px;
    font-weight: bold;
}
.about-contact-section{
    font-family: 'Roboto',sans-serif;
    font-size: 19px;
    line-height: 25px;
    color: black;
    width: 500px;
}
.contact-building-container{
    display: flex;
    flex-direction: row;
    gap: 10px;
    align-items: center;
    font-family: 'Rubik',sans-serif;
}
.contact-phone-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    font-family: 'Rubik',sans-serif;
}
.contact-mail-container{
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 25px;
    font-family: 'Rubik',sans-serif;
}
.right-inputField-contact-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 30px;
}
.first-last-name-field{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 20px;
}
label{
    font-family: 'Rubik',sans-serif;
    font-size: 17px;
}
.first-name-field{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
    
}
.last-name-field{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
}
#firstname,#lastname{
    height: 35px;
    border: 1px solid black;
    width: 250px;
    font-family: 'Roboto',sans-serif;
    font-size: 16px;
    padding-left: 3px;
    border-radius: 5px;
    background-color: transparent;
}
.email-field-container-contact{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
}
#email{
    height: 35px;
    border: 1px solid black;
    font-family: 'Roboto',sans-serif;
    font-size: 16px;
    padding-left: 3px;
    border-radius: 5px;
    background-color: transparent;
}
.phone-container-field-contact{
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 5px;
}
#phonenum{
    height: 35px;
    border: 1px solid black;
    font-family: 'Roboto',sans-serif;
    font-size: 16px;
    padding-left: 3px;
    border-radius: 5px;
    background-color: transparent;
}
.message-container-field-contact{
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 5px;
}
#message{
    height: 35px;
    border: 1px solid black;
    font-family: 'Roboto',sans-serif;
    font-size: 16px;
    padding: 3px;
    border-radius: 5px;
    height: 219px;
    background-color: transparent;
}
.button-contact-form-submit{
    display: flex;
    flex-direction: row;
    align-items: center;
}
#button-contact{
    width: 100%;
    height: 40px;
    font-family: 'Rubik',sans-serif;
    color: black;
    font-size: 20px;
    background: rgb(21, 195, 230);
    border: none;
    border-radius: 5px;
    border: 1px solid black;
    cursor: pointer;
}