/* Sabse uper ka Notify message  start*/
.top-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 40px;
    padding: 20px;
    overflow: hidden;
    background-color: rgb(21 195 237);
}

#top-notify-message {
    font-family: 'Roboto', sans-serif;
    word-spacing: 2px;
    font-size: 16px;
    color: white;
}
#mobileNum{
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    text-decoration: none;
    color:white;
}
#gmailId{
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    color: white;
    text-decoration: none;
}
.left-notify{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 10px;
}
#address{
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    color: white;
}
.top-icon-phone,.top-icon-email{
    color: white;
}
/* Notify message end */