@import url('https://fonts.googleapis.com/css2?family=Rubik&display=swap');
.container-find-place{
    width: 100%;
    height: 150px;
    background-color: #040E27;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    gap: 10px;
    padding: 50px 40px;
}
.input-bar-fields{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 25px;
}
#find-place-title{
    font-family: 'Rubik', sans-serif;
    font-size: 34px;
    margin-right: 30px;
    text-align: center;
    font-weight: 600;
    width: 100%;
    color: rgb(250, 248, 248);
}
.input-text-field{
    width: 220px;
    height: 45px;
    overflow: hidden;

}
#find-pace-text,#datepicker{
    width: 100%;
    padding: 5px;
    color: rgb(255, 255, 255);
    background: transparent;
    height: 100%;
    border-radius: 5px;
    font-family: 'Rubik',sans-serif;
    outline: none;
    border: 1px solid rgb(247, 247, 247) !important;
    font-size: 17px;
}
.select-option-place{
    width: 220px;
    height: 45px;
}
#select{
    width: 100%;
    height: 100%;
    border: 1px solid rgb(247, 247, 247) !important;
    outline: none;
    color: rgb(255, 255, 255);
    font-size: 17px;
    border-radius: 5px;
    background: transparent;
    font-family: 'Rubik',sans-serif;
}
.option-place{
    color: rgb(0, 0, 0);
    font-family: 'Rubik',sans-serif;
    border-radius:none ;
    padding: 5px 5px;
    font-size: 16px;
    border-bottom: 1px solid rgb(247, 247, 247);
}
.button-find-palce{
    width: 130px;
    height: 45px;
}
#button-search-palace{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: rgb(21 195 230);
    color: rgb(0, 0, 0);
    border-radius: 5px;
    font-size: 18px;
    outline: none;
    text-decoration: none;
    font-family: 'Rubik',sans-serif;
    transition: all 0.3s linear;
}
#button-search-palace:hover{
    background-color: rgb(21 102 200);
    color: white;
}