.prodcut-details-container{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: flex-start;
    gap: 10px;
    position: relative;
}
.left-details-co{
    width: 55%;
    height: 100%;
    position: sticky;
    top: 14%;
    display: flex;
    flex-direction: column;
    gap: 5px;
}
.box-container-small{
    width: 100%;
    display: flex;
    align-items: center;
    gap: 10px;
    justify-content: center;
}
.small-box{
    width: 120px;
    height: 80px;
    border: 1px solid black;
    background: transparent;

}
.main-product-box1{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 75vh;
}
.product-box2-details{
    width: 55%;
    height: 130vh;
    border: 1px solid black;
}