.blog-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 50px;
    align-items: center;
    justify-content: center;
    padding: 10px 10px;
    height: 600px;
    margin-top: 200px !important;
}
.top-blog-heading-container{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: center;
    justify-content: center;
    padding: 5px 5px;
}
.blog-title{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
}
#blog-title-section{
    font-family: 'Rubik', sans-serif;
    font-size: 40px;
    font-weight: bold;
}
#underline-text{
    position: relative;
}
#underline-text::after{
    content: '';
    position: absolute;
    bottom: 7px;
    width: 100%;
    height: 5px;
    background-color:  rgb(21 195 230);
    z-index: -9;
    left: 0%;
}
#text-section-blog{
    font-size: 18px;
    font-family: 'Rubik',sans-serif;
    width: 66%;
    text-align: center;
    color: #1f1e1e;
    line-height: 23px;
}
.about-blog-section{
    width: 100%;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center;
}
#actual-date{
    color: white;
    background-color: rgb(21 195 230);
    padding: 5px;
    font-size: 17px;
    width: 100px;
    font-weight: 600;
    border-radius: 10px;
    font-family: 'Rubik',sans-serif;
}
.container-bottom-blog-section{
    width: 100%;
    display: flex;
    flex-direction: row;
    height: 680px;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    padding: 5px 5px;
}
.left-blog{
    background-image: url(https://images.unsplash.com/photo-1682687982502-1529b3b33f85?ixlib=rb-4.0.3&ixid=M3wxMjA3fDF8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1470&q=80);
    background-position: center;
    background-size: cover;
}
.blog-section-cont{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 5px;
}
.left-blog{
    width: 100%;
    height: 65%;
    border-radius: 10px;
}
.right-blog{
    width: 100%;
    height: 35%;
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
    justify-content: center;
    padding: 2px;
}
.date-time{
    width: 100%;
    height: 50px;
}
.places-title{
    width: 100%;
    height: 50px;
}
#about-blog-title{
    color: black;
    font-family: 'Rubik',sans-serif;
    font-size: 30px;
}
.button-blog{
    width: 100%;
    height: 50px;
}
#button-see-more{
    width: 120px;
    font-family: 'Rubik',sans-serif;
    font-size: 17px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(21 195 230);
    color: black;
    height: 40px;
    text-decoration: none;
    border-radius:10px;
}
.about-places-description{
    width: 100%;
    height: 200px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
#about-place-summary{
    line-height: 23px;
    font-family: 'Rubik',sans-serif;
    font-size: 18px;
    color : rgb(63, 60, 60)
}

.small-blog-section-cont{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 5px;
    gap: 10px;
}
.blog-container-small{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
    padding: 3px;
}
.images-container-blog{
    width: 40%;
    height: 100%;
    border-radius: 10px;
    background-image: url(https://images.unsplash.com/photo-1469474968028-56623f02e42e?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=1474&q=80);
    background-position: center;
    background-size: cover;
}
.about-blog-container{
    width: 60%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    gap: 10px;
    padding: 3px;
}
.about-blog-container>a{
    text-decoration: none;
}
.date-time-of-blog{
    width: 100%;
    height: 30px;
}
.date-container{
    width: 80px;
    height: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(21 195 230);
    color: black;
    text-decoration: none;
    font-family: 'Rubik',sans-serif;
    border-radius: 10px;
}
.blog-title-text{
    width: 100%;
    height: 30px;
}
.title-blog-container-small-box{
    font-family: 'Rubik',sans-serif;
    font-size: 25px;
    color: black;
}
.about-blog-description-small{
    width: 100%;
    height: 100%;
}
.description-about-small-blog-box{
    font-family: 'Rubik',sans-serif;
    font-size: 16px;
    color : rgb(63, 60, 60);
}