@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

.navbar-container {
    width: 100%;
    background-color: #ffffffed;
    height: 80px;
    position: sticky;
    box-shadow: 1px 1px 10px rgb(190, 183, 183);
    z-index: 100;
    top: 0%;
}

.nav-container {
    width: 90%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin: auto;
}

.logo-container {
    width: 290px;
    height: 120px;
    margin-left: -89px;
}

#logo-nav {
    display: inline-block;
    height: 100%;
    width: 100%;
}

.links {
    display: flex;
    align-items: center;
    justify-content: center;
}

#page-links-container {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    list-style-type: none;
    margin-left: -50px;
}

.button-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.link {
    color: black;
    text-decoration: none;
    margin: 10px 30px;
    display: flex;
    justify-content: center;
    font-family: 'Roboto', sans-serif;
    font-size: 17px;
    font-weight: 500;
    position: relative;
    gap: 7px;
}

.link::after {
    position: absolute;
    content: '';
    bottom: -4px;
    width: 0%;
    left: 0%;
    background-color: rgb(21 195 230);
    height: 2px;
    transition: width 0.5s linear;
}

.link:hover::after {
    width: 100%;
}

#btn-contact {
    color: white;
    background-color: rgb(21 195 230);
    text-decoration: none;
    font-size: 17px;
    padding: 10px;
    width: 150px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    font-family: 'Roboto', sans-serif;
    border-radius: 10px;
    transition: all 0.5s linear;
}
.bars-slider{
    display: none;
}
#bar{
    font-size: 28px;
    padding-right: 15px;
    color: rgb(21, 195, 235);
    -webkit-tap-highlight-color: transparent;
}

#btn-contact:hover {
    background-color: rgb(21 102 200);
    box-shadow: 1px 1px 7px grey;
    color: white;
    cursor: pointer;
}

/* Add styles for the submenus */
.link-dropdown{
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 10px;
}
.about{
    margin-left: 30px;
}
.phone-contact{
    font-size: 15px;
    padding-left: 5px;
}
.lessThanSymbol{
    position: absolute;
    color: black;
    top: 6px;
    font-weight: bold;
    left: 100%;
    font-size: 8px !important;
}
.submenu {
    display: none;
    position: absolute;
    background-color: rgba(255, 255, 255);
    list-style-type: none;
    padding: 5px;
    margin: 5px;
    width: 180px;
    top: 85%;
    transition: all 0.5s linear;
    left: 13%;
    border: 1px solid rgb(192, 192, 192);
}


.submenu .submenu-list {
    
    width: 100%;
    border-bottom: 1px solid rgb(173, 173, 173);
    padding: 10px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
   
}
.honeymoon,.agra,.adventure{
    border: none !important;
}

.submenu .submenu-link {
    padding-left: 5px;
    color: black;
    text-decoration: none;
    position: relative;
    font-family: 'Roboto', sans-serif;

}
.submenu-link::after{
    position: absolute;
    content: '';
    bottom: -3px;
    width: 0%;
    left: 6px;
    background-color: rgb(21 195 230);
    height: 2px;
    transition: all 0.5s linear;
}
.submenu-link:hover::after{
    width: 50%;
}

.link-dropdown:hover .submenu {
    display: block;
    transition: all 0.5s linear;
}
.submenu-image{
    width: 25px;
    height: 25px;
    object-fit: cover;
}
.Link{
    text-decoration: none;
}
/* Rest of your existing styles remain the same */
