#thumbnail-div{
    justify-content: center;
    display: none !important;
}
.thumbnail{
    width: 80px;
    object-fit: cover;
    height: 60px;
}
.active-thumbnail{
    border: 2px solid rgb(21 195 237) !important;
    padding: 2px;
}
.carousel-caption-bottom{
    font-family: 'Roboto', sans-serif;
}
.dot.active{
    background-color: rgb(21 195 237);
}
.dot.active:hover{
    background-color: black !important;
}
.prev:hover,.next:hover{
    background-color: rgb(21, 195, 237) !important;
}