.guide-container{
    display: flex;
    width: 100%;
    margin: auto;
    margin-top: 100px;
    align-items: center;
    justify-content: space-around;
    gap: 25px;
    margin-bottom: 20px;
    flex-wrap: wrap;
}
.journey-container,.hotel-container,.travel-guide-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 15px;
}
#about-brand1,#about-brand2,#about-brand3{
    font-family: 'Rubik',sans-serif;
    font-size: 15px;
    color: rgb(22, 22, 22);
    text-align: center;
    line-height: 20px;
}
.about-brand{
    width: 300px;
}
.title-brand{
    font-family: 'Rubik',sans-serif;
    font-size: 12px;
    font-weight: 500;
}
.image-journey>img{
    filter: invert(1);
}