.places-container {
    padding-top: 100px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 100px;
    overflow:hidden ;
    z-index: -10;
    background-color: white;
}

.place-box-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    width: 100%;
    gap: 70px;
}

.heading-section-places {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 15px;
    margin: auto;
}

.popular-places-heading {
    font-family: 'Rubik', sans-serif;
    font-size: 40px;
    font-weight: bold;
}

.places {
    position: relative;
    z-index: 10;
}

.places::after {
    position: absolute;
    content: '';
    bottom: 7px;
    width: 100%;
    left: 0%;
    height: 6px;
    background-color: rgb(21 195 230);
    z-index: -9;
}

.description-about-places {
    width: 70%;
    text-align: center;
}

.description-about-places {
    font-family: 'Rubik', sans-serif;
    font-size: 17px;
    line-height: 25px;
    text-align: center;
}


/* box */
.showcase {
    background-color: transparent;
    display: flex;
    justify-content: center;
    align-items: center;
}

.travel-card {
    position: relative;
    display: inline-flex;
    flex-flow: column nowrap;
    font-weight: 300;
    background-color: white;
    border: 1px solid rgb(207, 207, 207);
    border-radius: 15px;
    width: 350px;
    text-decoration: none;
    transition: all 0.3s ease;


    &:hover>.image>img {
        transform: scale(1.2);
    }

    & .image {
        width: 100%;
        height: 250px;
        border-top-left-radius: inherit;
        border-top-right-radius: inherit;
        overflow: hidden;

        &>img {
            width: 100%;
            height: 100%;
            background-image: url('https://cache-graphicslib.viator.com/graphicslib/page-images/742x525/467829_Viator_Unsplash_170410.jpg');
            background-position: center;
            background-size: cover;
            border-top-left-radius: inherit;
            border-top-right-radius: inherit;
            transition: all .28s ease-in-out;
        }
    }

    &>.content {
        color: #545454;
        padding: 1em 1.5em;
        transition: all 0.3s ease;

        &> :nth-child(1n+2) {
            margin-top: 1em;
        }

        &>.category {
            font-size: 1.2em;
            color: #bababa;
        }

        &>.topic {
            font-size: 1.3em;
            line-height: calc(1rem * 2.8);
            word-break: break-all;
            max-height: calc(1rem * 2.8 * 2);
            overflow: hidden;
            position: relative;
            background-color: white;    
            font-family: 'Rubik',sans-serif;
            color: black;
        }

        &>.recommendation {
            display: flex;
            align-items: center;

            &>.score {
                display: inline-flex;
                justify-content: center;
                align-items: center;
                background: rgb(254, 207, 139);
                background: linear-gradient(130deg, rgba(254, 207, 139, 1) 0%, rgba(255, 198, 115, 1) 75%, rgba(255, 187, 88, 1) 100%);
                color: white;
                padding: 5px 8px;
                border-radius: 30px;
                user-select: none;

                &> :first-child {
                    padding-top: 1px;
                    padding-left: 4px;
                }

                &> :last-child {
                    font-size: 1.1em;
                    padding-left: 6px;
                }
            }

            &>.comment {
                color: #bababa;
                margin-left: 1.5em;
            }
        }

        &>.price {
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-family: 'Rubik', sans-serif;

            &>.discount-info {
                color: #4fc3f7;
            }

            &>.original-price {
                font-size: 2em;
            }
        }
    }
}

.viewMore-btn {
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Rubik', sans-serif;
    font-size: 18px;
    background-color: rgb(21 195 230);
    color: black;
    cursor: pointer;
    width: 200px;
    height: 50px;
    text-decoration: none;
    margin-bottom: 20px;
    border-radius: 10px;
    transition: all 0.3s ease;
}

.viewMore-btn:hover {
    background-color: rgb(21, 102, 200);
}



