/* Keep the existing CSS for text and button styling */

.video-container {
    width: 100%;
    height: 600px;
    margin-top: 100px;
}
/* Keep the existing styles for the nature-img */
#nature-img {
    object-fit: cover;
    width: 100%;
    position: relative;
    background-position: center;
    background-size: cover;
    display: flex;
    height: 100%;
    background-image: url(https://preview.colorlib.com/theme/travelo/img/banner/video.png.webp);
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 30px;
}

/* Keep the existing styles for text and button */
#text-video {
    font-family: 'Rubik', sans-serif;
    font-weight: 400;
    font-size: 60px;
    color: white;
}

#play-video-btn {
    background-color: rgb(21 195 230);
    border: none;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    cursor: pointer;
    outline: none;
    text-decoration:none;
    position: relative;
}

#play-icon {
    color: #fff;
    font-size: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

#play-video-btn::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0%;
    left: 0%;
    right: 0%;
    border-radius: 50%;
    background: rgb(21 195 230);
    animation: wave-animation 2s linear infinite;
}

@keyframes wave-animation {
    0% {
        transform: scale(1);
        opacity: 1;
    }

    100% {
        transform: scale(1.8);
        opacity: 0;
    }
}