.destination-container-section{
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: 100px;
    gap: 100px;
    padding: 20px 10px;
}
.destination-title-description-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 15px;
    margin: auto;
}
#title-destination{
    font-family: 'Rubik',sans-serif;
    font-size: 40px;
    font-weight: bold;
}
#destination_span{
    position: relative;
}
#description-destination{
    width: 70%;
    text-align: center;
}
#destination_span::after{
    position: absolute;
    content: '';
    bottom: 7px;
    width: 100%;
    left: 0%;
    height: 6px;
    background-color: rgb(21 195 230);
    z-index: -9;
}
#description-destination{
    font-family: 'Rubik',sans-serif;
    font-size: 17px;
    line-height: 25px;
    text-align: center;
}
.destination-paces{
    display: flex;
    gap: 20px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.city_popular_place{
    height: 300px;
    width: 300px;
    border: 1px solid rgb(236, 234, 234);
    display: flex;
    position: relative;
    align-items: flex-end; 
    transition: all 0.3s ease-in-out;
}

#city{
    font-size: 30px;
    font-weight: 600;
    font-family: 'Rubik',sans-serif;
    color: white;
}
#places_in_city{
    background-color: rgb(21 195 230);
    display: inline-flex;
    justify-content: center;
    align-items: center;
    color: white;
    width: 80px;
    cursor: pointer;
    border-radius: 15px;
    padding: 2px;
    font-family: 'Rubik',sans-serif;
    font-weight: 400;
    height: 30px;
    text-decoration: none;
    font-size: 16px;
    transition: background 0.3s linear;
}
.information-place{
    position: absolute;
    display: flex;
    gap: 13px;
    left: 10px;
    bottom: 15px;
    justify-content: center;
    align-items: center;
}

.city_popular_place:hover {
    transform: translateY(-15px); /* Move the box 20px up when hovering */
}
#places_in_city:hover{
    background-color: rgb(21 102 200);
}