.footer-container{
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    margin-top: 100px;
    gap: 20px;
    box-shadow: 1px 1px 7px grey;
}
.social-media-icons-container{
    width: 100%;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-between;
    height: 60px;
    padding: 0px 10px;
    border-bottom: 1px solid rgb(214, 212, 212);
}
.all-pages-link-container{
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    padding: 0px 10px;
}

.box2,.box3,.box4{
    display: flex;
    width: 100%;
    flex-direction: column;
    gap: 20px;
    justify-content: center;
    align-items: center;
}
.company-logo{
    width: 180px;
    height: 180px;
    background-position: center;
    background-size: cover;
    filter: invert(1);
}
.copyright-container{
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    padding: 0px 10px;
    height: 50px;
    border-top: 1px solid rgb(214, 212, 212);
}
.links-pages-footer{
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: center;
    justify-content: center;
    list-style-type: none;
    font-family: 'Rubik',sans-serif;
}
.links-pages-footer>li>a{
    text-decoration: none;
    color: black;
}
.title-footer-links{
    font-family: 'Roboto',sans-serif;
    font-size: 18px;
    text-transform: uppercase;
}
#mess-footer{
    font-size: 18px;
    font-family: 'Rubik',sans-serif;
    font-weight: 500;
}
.social-media-icons>ul>a{
    padding: 0px 10px;
    font-size: 20px;
    color: black;
}

#copyright{
    font-size: 18px;
    font-family: 'Rubik',sans-serif;
}
.icon-footer-social{
    padding-right: 5px;
}